@import url("https://fonts.googleapis.com/css2?family=Nova+Mono&family=Quicksand:wght@300;

400;500;600;700&display=swap");
@font-face {
  font-family: "jmh";
  src: url("../src/fonts/jmh_psychedelic_caps-webfont.woff") format("woff"), url("../src/fonts/jmh_psychedelic_caps-webfont.woff2") format("woff2");
}

:root {
  --mainbg: url("../src/images/bg2.png");
  --gradient1: linear-gradient(
    to bottom right,
    rgba(255, 65, 108, 0.75) 0%,
    rgba(255, 75, 43, 0.75) 100%
  );
  --gradient2: linear-gradient(
    to bottom right,
    rgba(195, 20, 50, 0.75) 0%,
    rgba(36, 11, 54, 0.75) 100%
  );
  --gradient3: linear-gradient(
    to bottom right,
    rgba(255, 65, 108, 0.25) 0%,
    rgba(255, 75, 43, 0.25) 100%
  );
  --font1: "jmh";
  --font2: "Nova Mono", monospace;
  --font3: "Quicksand", sans-serif;
  --unit: 8px;
  --mclr: 195, 20, 50;
  --hclr: 207, 139, 243;
  --gclr: 253, 185, 155;
  --c1: 167, 112, 239;
  --c2: 207, 139, 243;
  --c3: 253, 185, 155;
  --hx1: #ff416c;
  --hx2: #c31432;
  --hx3: #240b36;
  --x4: #40e0d0;
  --x5: #ff8c00;
  --x6: #ff0080;
}

.thrivin-post {
  width: 100%;
  background: #fff;
  border: 1px solid #ddd;
  display: flex;
  flex-direction: column;
  font-size: 16px;
}

.thrivin-post .post-author {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  background: #111;
  border-bottom: 1px solid #333;
}

.thrivin-post .post-author .author-details {
  width: 700px;
  padding: 45px;
  color: #fff;
}

.thrivin-post .post-author .author-details .author-name a {
  font-family: var(--font3);
  font-size: 45px;
  letter-spacing: -1px;
  text-transform: uppercase;
  display: inline-block;
}

.thrivin-post .post-author .author-details .author-name a:after {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  content: "";
  display: block;
  width: 90%;
  margin-left: -45px;
  height: 6px;
  border-radius: 6px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  background: linear-gradient(to right, rgba(var(--mclr), 1), rgba(var(--hclr), 1), rgba(var(--gclr), 1));
}

.thrivin-post .post-author .author-details .author-basics {
  font-family: var(--font4);
  font-size: 12px;
  text-transform: lowercase;
  margin-top: 1.5rem;
}

.thrivin-post .post-author .author-details .author-basics span.sepa {
  color: var(--x2);
  display: inline-block;
  margin: 0 1rem;
}

.thrivin-post .post-author .author-gif {
  width: 348px;
  padding: 25px;
}

.thrivin-post .post-author .author-gif .gif-wrap {
  width: 100%;
  height: 200px;
  background: linear-gradient(to bottom right, rgba(var(--mclr), 1), rgba(var(--hclr), 1), rgba(var(--gclr), 1));
  padding: 6px;
}

.thrivin-post .post-author .author-gif .gif-wrap img {
  width: 100%;
  height: 100%;
  border: 6px solid #111;
  object-fit: cover;
}

.thrivin-post .post-details {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  background: #111;
  color: #fff;
  padding: 15px 25px;
}

.thrivin-post .post-details .post-date {
  font-size: 12px;
  font-family: var(--font4);
  text-transform: lowercase;
}

.thrivin-post .post-details .post-date i {
  padding-bottom: 4px;
  background: linear-gradient(to right, rgba(var(--mclr), 1), rgba(var(--hclr), 1), rgba(var(--gclr), 1)) no-repeat bottom left/100% 1px;
}

.thrivin-post .post-details .post-btns a {
  background: #181818;
  border: 1px solid #333;
  padding: 10px 12px;
  font-size: 10px;
  font-family: var(--font4);
  transition: 0.25s all 0.1s ease;
}

.thrivin-post .post-details .post-btns a:hover {
  color: #111;
  background: #fafafa;
}

.thrivin-post .post-details .post-btns a span {
  background: linear-gradient(to right, rgba(var(--mclr), 1), rgba(var(--hclr), 1), rgba(var(--gclr), 1)) no-repeat bottom left/100% 1px;
  padding-bottom: 4px;
}

.thrivin-post .post-contents {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  border-bottom: 1px solid #333;
}

.thrivin-post .post-contents .post-content {
  width: 733px;
  background: #fafafa;
}

.thrivin-post .post-contents .post-content .post-reply {
  padding: 45px;
}

.thrivin-post .post-contents .mini-profile {
  width: 315px;
  border-right: 1px solid #ddd;
  padding: 25px;
}

.thrivin-post .post-contents .mini-profile .nickname {
  width: 100%;
  text-align: center;
}

.thrivin-post .post-contents .mini-profile .nickname i {
  font-family: var(--font4);
  text-transform: lowercase;
  font-size: 14px;
}

.thrivin-post .post-contents .mini-profile .nickname span {
  display: block;
  font-family: var(--font3);
  text-transform: uppercase;
  font-size: 28px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  background: linear-gradient(-90deg, rgba(var(--mclr), 1) 0%, rgba(var(--hclr), 1) 50%, rgba(var(--gclr), 1) 100%);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}

.thrivin-post .post-contents .mini-profile .nickname group {
  font-family: var(--font2);
  font-size: 10px;
  text-transform: uppercase;
  display: block;
}

.thrivin-post .post-contents .mini-profile .nickname group strong:before {
  content: var(--name);
  color: var(--x1);
  background: linear-gradient(-90deg, rgba(var(--mclr), 1) 0%, rgba(var(--hclr), 1) 50%, rgba(var(--gclr), 1) 100%);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}

.thrivin-post .post-contents .mini-profile .avatar-wrap {
  margin-top: 3rem;
  margin-bottom: 3rem;
  padding: 6px;
  width: 100%;
  height: 433px;
  background: linear-gradient(to left bottom, rgba(var(--mclr), 1) 0%, rgba(var(--hclr), 1) 50%, rgba(var(--gclr), 1) 100%);
}

.thrivin-post .post-contents .mini-profile .avatar-wrap img {
  width: 100%;
  height: 100%;
  border: 6px solid #fff;
  object-fit: cover;
}

.thrivin-post .post-contents .mini-profile .author-stats {
  margin-bottom: 3rem;
  font-family: var(--font2);
  letter-spacing: 1px;
  text-align: center;
  text-transform: uppercase;
  font-size: 9px;
}

.thrivin-post .post-contents .mini-profile .traits {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}

.thrivin-post .post-contents .mini-profile .traits span {
  text-align: center;
  background-color: #fafafa;
  background-position: center;
  background-size: 65%;
  background-repeat: no-repeat;
  width: 84px;
  margin-bottom: 0.5rem;
  height: 84px;
  display: inline-block;
  border: 1px solid #ddd;
  border-radius: 2px;
  padding: 14px;
}

.thrivin-post .post-contents .mini-profile .mp-gif {
  width: 100%;
  margin-top: 2.5rem;
  background: linear-gradient(to left bottom, rgba(var(--mclr), 1) 0%, rgba(var(--hclr), 1) 50%, rgba(var(--gclr), 1) 100%);
  height: 200px;
  border: 1px solid #ddd;
  padding: 6px;
}

.thrivin-post .post-contents .mini-profile .mp-gif img {
  border: 6px solid #fff;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.thrivin-post .post-footer {
  background: #111;
  display: flex;
  flex-flow: row wrap;
  padding: 25px;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #333;
}

.thrivin-post .post-footer .footer-gif {
  width: 100px;
  border-radius: 100%;
  height: 100px;
  background: linear-gradient(to left bottom, rgba(var(--mclr), 1) 0%, rgba(var(--hclr), 1) 50%, rgba(var(--gclr), 1) 100%);
  padding: 1px;
}

.thrivin-post .post-footer .footer-gif img {
  width: 100%;
  height: 100%;
  border: 3px solid #111;
  object-fit: cover;
  border-radius: 100%;
}

.thrivin-post .post-footer .author-relationship {
  width: 450px;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}

.thrivin-post .post-footer .author-relationship .rel-info {
  width: 334px;
}

.thrivin-post .post-footer .author-relationship i {
  display: block;
  font-family: var(--font4);
  font-size: 14px;
  color: #fff;
  text-transform: lowercase;
}

.thrivin-post .post-footer .author-relationship strong {
  font-weight: 400;
  text-transform: uppercase;
  color: var(--x3);
  font-family: var(--font2);
  font-size: 10px;
  letter-spacing: 1px;
  display: block;
}

.thrivin-post .post-footer .author-relationship span {
  font-family: var(--font3);
  font-size: 24px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #fff;
}

.thrivin-post .post-footer .author-relationship .rel-gif {
  margin-top: 0.5rem;
  margin-right: 2rem;
  width: 100px;
  padding: 3px;
  background: linear-gradient(to left bottom, rgba(var(--mclr), 1) 0%, rgba(var(--hclr), 1) 50%, rgba(var(--gclr), 1) 100%);
  height: 100px;
}

.thrivin-post .post-footer .author-relationship .rel-gif img {
  width: 100%;
  height: 100%;
  border: 3px solid #111;
  object-fit: cover;
}

.thrivin-post .post-author-contact {
  background: #111;
  padding: 15px 25px;
  font-family: var(--font4);
  color: #fff;
  font-size: 12px;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.thrivin-post .post-author-contact .group-ani {
  width: 50px;
  height: 50px;
  background-size: cover;
  background-image: var(--ani);
}

.thrivin-post .post-author-contact span i {
  color: var(--x3);
}

.thrivin-post .post-author-contact a {
  font-family: inherit;
  font-size: inherit;
  transition: 0.25s all 0.1s ease;
}

.thrivin-post .post-author-contact a:hover {
  color: var(--x2);
}